import utils from "../../../utils.js";
export default {
	onShareAppMessage: function(res) {
		
		if (res.from == 'button') {
			
		}
		return {
			title: '忽微交通工程智慧安全',
			path: '/pages/basic/login/login',
			imageUrl: 'http://huwei-soft.com/HWWechat/static/logo.png',
			success: function(res) {
				uni.showToast({
				    title: '分享成功',
				    duration: 2000
				});
			}
		};
	},
	data() {
		return {
			userInfo: {},
			todoNum: '',
			active: 1,
			scanMsg: '',
			scanType: '',
			header: '主页',
			menuArrow: true,
			menuBorder: false,
			menuCard: false,
			modalName: null,
			show: false,
			BuilderId: null,
			isCheckIn: false
		};
	},
	methods: {
		gotoScore() {
			uni.navigateTo({
				url: '/pages/some/score/score'
			})
		},
		gotoFile() {
			uni.navigateTo({
				url: '/pages/some/dangerFile/dangerFile'
			})
		},
		showModal(e) {
			this.modalName = "Modal"
			console.log(e)
		},
		hideModal(e) {
			this.modalName = null
		},
		//授权
		shouquan() {
			var self = this;
			uni.authorize({
				scope: 'scope.userLocation',
				success(res) {
					
				}
			});
		},
		hide() {
			uni.hideLoading();
		},
		showModel(content) {
			uni.showModal({
			    title: '提示',
			    content: content,
					showCancel: false,
			    success: function (res) {
			        if (res.confirm) {
			            
			        }
			    }
			});
		},
		reloginCount() {
			uni.showModal({
				title: '提示',
				content: '退出登录?',
				success: function(res) {
					if (res.confirm) {
						uni.reLaunch({
							url: '/pages/basic/login/login'
						});
					} else if (res.cancel) {
						
					}
				}
			});
		},
		getTodoNum() {
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/Process/Get_Mytask',
					data: {
						RYXH: self.$store.state.userInfo.RYXH
					}
				})
				.then(data => {
					var [error, res] = data;
					
					if (res.data == '暂无数据') {
						self.todoNum = '';
					} else {
						let data = JSON.parse(res.data);
						let len = data.length;
						len = len > 99 ? 99 : len;
						self.todoNum = len;
					}
				});
		},

		linkTo(name) {
			
			switch (name) {
				case 'home':
					this.active = 1;
					this.header = '首页';
					break;
				case 'search':
					this.active = 2;
					this.header = '搜索';
					break;
				case 'todo':
					this.active = 4;
					this.header = '待办事项';
					break;
				case 'my':
					this.active = 5;
					this.header = '个人中心';
					break;
			}
		},
		
		gotohome() {
			this.active = 1;
			this.header = '首页';
			this.getTodoNum();
		},
		toTodo() {
			this.active = 4;
			this.header = '待办事项';
			this.getTodoNum();
		}
	},
	mounted() {
		this.userInfo = this.$store.state.userInfo;
		var foo = JSON.parse(this.userInfo.Module);
		this.isCheckIn = foo.checkIn;
		console.log(this.isCheckIn)
		
		
		this.shouquan();
		this.getTodoNum();
	}
};
